.sk-featured-images-hero {
  padding-top: 45px !important;
  padding-bottom: 0 !important;
}
.sk-featured-images-hero .media-block {
  align-items: unset;
}
.sk-featured-images-hero .media-block .img-content {
  flex: unset;
  max-height: none;
  max-width: none;
  width: auto;
  overflow: visible !important;
}
@media screen and (max-width: 1024px) {
  .sk-featured-images-hero {
    margin-top: 0px !important;
  }
  .sk-featured-images-hero .media-block {
    flex-direction: column-reverse;
  }
  .sk-featured-images-hero.sk-block-0 {
    padding-top: 50px !important;
  }
  .sk-featured-images-hero .img-wrap {
    margin-bottom: 10px;
  }
}
@media (max-width: 1024px) {
  .sk-featured-images-hero .col-sm-12.col-lg-6 {
    width: 100%;
  }
}
.sk-featured-images-hero .featured-img,
.sk-featured-images-hero .featured-img img {
  border-radius: 26.78px;
  padding: 7px;
}
@media screen and (min-width: 1025px) and (max-width: 1199px) {
  .sk-featured-images-hero .featured-img,
  .sk-featured-images-hero .featured-img img {
    padding: 3px;
  }
}
@media screen and (max-width: 1024px) {
  .sk-featured-images-hero .featured-img,
  .sk-featured-images-hero .featured-img img {
    padding: 3px;
  }
}
.sk-featured-images-hero .featured-img {
  margin-bottom: 0;
}
.sk-featured-images-hero .featured-img img {
  width: 100%;
}
.sk-featured-images-hero .image-content {
  padding-bottom: 2px;
}
.sk-featured-images-hero.bg-blue.sk-case-studies .container .media-block .img-wrap.d-desktop-flex {
  margin-bottom: 0 !important;
}
.sk-featured-images-hero .txt-content {
  margin-top: 0 !important;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
}
@media screen and (min-width: 1025px) {
  .sk-featured-images-hero .txt-content {
    padding-bottom: 0;
  }
}
@media screen and (max-width: 1024px) {
  .sk-featured-images-hero .txt-content h1 {
    font-size: 9.143vw;
    line-height: 11.714vw;
  }
}
@media screen and (min-width: 1025px) and (max-width: 1199px) {
  .sk-featured-images-hero .txt-content {
    line-height: 75px;
  }
  .sk-featured-images-hero .txt-content h1 {
    font-size: 50px;
    line-height: 65px;
  }
}
.sk-featured-images-hero .txt-content .btn-scroll-next {
  position: relative;
  width: auto;
  margin-bottom: 50px;
}
.sk-featured-images-hero .img-row-0, .sk-featured-images-hero .img-row-2, .sk-featured-images-hero .img-row-3, .sk-featured-images-hero .img-row-5 {
  align-content: flex-end;
  padding-bottom: 0;
}
.sk-featured-images-hero .img-row-1, .sk-featured-images-hero .img-row-4 {
  align-content: flex-start;
  top: -45px;
  position: relative;
}